.dark .list{
  min-height: 100vh;
  background: #1F242B;
}
.dark .list .box{
  border-radius: 4px;
  border: 0;
  background: #fff;
  box-shadow: 0px 0px 6px 0 rgba(102, 102, 102, .1)
}
.dark .catalog > p{
  color: #333333;
}
.dark .list h2{
  color: #333;
  background: transparent;
}
/**/
.dark .content{
  background: #1F242B;
}
.dark .touch{
  background: #333;
}
.dark .touch p{
  color: #999999;
}
/**/
.dark .message section{
  border: 10px solid;
  border-image-source: url(./border.png);
  border-image-slice: 20 20 20 20 fill;
  border-image-width: 10px;
  border-image-repeat: round;  
}
.dark .message p{
  color: #333333 !important;
}
.dark .main .message section{
  border: 10px solid;
  border-image-source: url(./border-main.png);
  border-image-slice: 20 20 20 20 fill;
  border-image-width: 10px;
  border-image-repeat: round;  
}
.dark .main .message  p{
  color: #333;
}
.dark .other p{
  color: #617082;
}

/*navigator*/
.dark .nav{
  background: #31363C;
}
.dark .icon-back{
  background: url(./back.png) no-repeat center center;
}
.dark  .icon-theme{
  background: url(./theme.png) no-repeat center center;
}
.dark .icon-catalog{
  background: url(./catalog.png) no-repeat center center;
}
.dark .icon-share{
  background: url(./share.png) no-repeat center center;
}
/**/
.dark .endding{
  background: #fefefe;
}


