
.list{
  font-size: .4rem;
}
.list ul{
  width: 10rem;
  padding: 8px 0;
  text-align: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.list li{
  position: relative;
  display: inline-block;  
  width: 5rem;
  padding: 0 4px 0 8px;
  margin-bottom: 8px;  
  -webkit-box-sizing: border-box;  
          box-sizing: border-box;
}
.list li:nth-of-type(2n){
  padding-right: 8px;
  padding-left: 4px;
}
.list .box{
  position: relative;
  border-radius: 5px;
}
.cover{
  position: relative;
  height: 7.5rem;
}
.list li a{
  display: block;
}
.list .album .cover{
  height: 1.6rem;
}
.list .album img{
  height: 1.6rem;
}
.list .album .box::after{
  display: none;
}
.cover img{  
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 7.5rem;
  border-radius: 5px;
}
.cover > p{
  position: absolute;
  z-index: 11;
  left: 0;
  right: 0;
  top: 10px;
  line-height: 25px;
  font-size: .3rem;  
  text-align: left;
  text-indent: 10px;
}
.cover p small{
  float:right;
  margin-right: 10px;
}
.list h2{
    position: absolute;
    left: 0;    
    width: 100%;
    bottom: 0;
    text-align: left;
    font-size: .3rem;
    line-height: .35rem;
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    opacity: 0.9;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    /* text-indent: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; */
}



