
.navigator{
  font-size: .35rem;  
  color: #333;
}
.navigator .buttons{
  float: right;
}
.navigator a{
  display: inline-block;
}
.navigator .buttons {
  margin-right: 20px;
}
.navigator .buttons i{
  margin: 0 15px;
  vertical-align: middle;
}
.icon-back{
  display: inline-block;
  width: 0.5rem;
  height: 0.5rem;  
  vertical-align: middle;
  background: transparent;
   background-size: contain !important;
  margin-left: 20px;
  margin-top: -3px;
}
.icon-theme{
  display: inline-block;
  width: 0.5rem;
  height: 0.5rem;  
  background-size: contain !important;
}
.icon-catalog{
  display: inline-block;
  width: 0.5rem;
  height: 0.5rem;  
  background-size: contain !important;
}
.icon-share{
  display: inline-block;
  width: 0.5rem;
  height: 0.5rem;  
  background-size: contain !important;
}



