.default .list{
  min-height: 100vh;
  background: #F5F8FA;
}
.default .list .box{
  border-radius: 4px;
  border: 0;
  background: #fff;
  box-shadow: 0px 0px 6px 0 rgba(102, 102, 102, .3)
}
.default .catalog > p{
  color: #333333;
}
.default .list h2{
  color: #333;
  background: #fff;
  opacity: 0.8;
}
/**/
.default .content{
  background: #F3F3F3;
}
.default .touch{
  background: #fff;
}
.default .touch p{
  color: #999999;
}
/**/
.default .message section{
  border: 10px solid;
  border-image-source: url(./border.png);
  border-image-slice: 20 20 20 20 fill;
  border-image-width: 10px;
  border-image-repeat: round;  
}
.default .message p{
  color: #333333 !important;
}
.default .main .message section{
  border: 10px solid;
  border-image-source: url(./border-main.png);
  border-image-slice: 20 20 20 20 fill;
  border-image-width: 10px;
  border-image-repeat: round;  
}
.default .main .message  p{
  color: #333;
}
.default .other p{
  color: #999;
}

/*navigator*/
.default .nav{
  background: #fff;
}
.default .icon-back{
  background: url(./back.png) no-repeat center center;
}
.default  .icon-theme{
  background: url(./theme.png) no-repeat center center;
}
.default .icon-catalog{
  background: url(./catalog.png) no-repeat center center;
}
.default .icon-share{
  background: url(./share.png) no-repeat center center;
}
/**/
.default .endding{
  background: #fefefe;
}


